import React, { useEffect, useMemo } from "react";
import { FormItem, Select } from "@vkontakte/vkui";
import { $categories } from "../../../../store/categories";
import { useUnit } from "effector-react";
import { getAllCategoriesFX } from "../../../../api/server/categories/categories";

type Props = {
  value: string | undefined
  onChange: (e: any) => void
  isRequired: boolean
};

function CategorySelect({ value, onChange, isRequired }: Props) {

  const categories = useUnit($categories);

  useEffect(() => {
    getAllCategoriesFX();
  }, []);

  const createOptions = useMemo(() => {
    const options = categories.map(category => ({
      value: category.title,
      label: category.title
    }));
    return options;
  }, [categories])

  return (
    <FormItem
      top=''
      htmlFor="bookCategory"
    >
      <Select
        defaultValue=''
        id="bookCategory"
        placeholder="  "
        name="genre"
        value={value}
        onChange={onChange}
        required={isRequired}
        options={createOptions} />
    </FormItem>
  );
}

export default React.memo(CategorySelect);
