import { FormItem, Select } from "@vkontakte/vkui";
import { qualityOptions } from "../../../../constants/utils";

type Props = {
  value: string | undefined
  onChange: (e: any) => void
  isRequired: boolean
}

export default function QualitySelect({ value, onChange, isRequired }: Props) {
  return (
    <FormItem
      top=''
      htmlFor="bookQuality"
    >
      <Select
        defaultValue=''
        id="bookQuality"
        placeholder="  "
        name="bookQuality"
        value={value}
        onChange={onChange}
        required={isRequired}
        options={qualityOptions} />
    </FormItem>
  )
}